import React from "react";
import { useNavigate } from "react-router-dom";
import image from "../images/754556407.png";
import { TextField } from "@mui/material";

export default function Payment() {
  let navigate = useNavigate();
  const handleSubmit = async (e) => {
    e.preventDefault();
    navigate("/employMain");
  };

  return (
    <>
      <h4>Thanh Ton Vi Momo</h4>
      <img src={image} alt="" style={{ width: "18%", height: "18%" }} />
      <div>
        <h5>
          Hy m app Momo ln  qut m thanh ton hoc bm chuyn tin vo s
          ti khon MOMO Nguyn Hunh Tun
        </h5>
        <h3>S tin 10.000VN/1 bi</h3>
        <h5>Li nhn: CKDAYBAI</h5>
        <h5>Sau khi donate xong bn s c m giao dch ca Momo</h5>
        <h5>
          Hy nhp m giao dch vo  bn di, h thng s t xc nhn trong
          vng 5 pht
        </h5>
      </div>
      <div>
        <h3>Thng tin giao dch</h3>
        <form onSubmit={handleSubmit}>
          <div
            style={{ display: "flex", justifyContent: "center", gap: "20px" }}
          >
            <TextField
              required
              id="standard-required"
              label="Required"
              helperText="ST ca bn"
              variant="standard"
            />
            <TextField
              required
              id="standard-required"
              label="Required"
              helperText="M giao dch"
              variant="standard"
            />
          </div>

          <button
            class="cssbuttons-io-button"
            type="submit"
            style={{ marginTop: "10px", marginBottom: "10px" }}
          >
            Next
            <div class="icon">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                width="24"
                height="24"
              >
                <path fill="none" d="M0 0h24v24H0z"></path>
                <path
                  fill="currentColor"
                  d="M16.172 11l-5.364-5.364 1.414-1.414L20 12l-7.778 7.778-1.414-1.414L16.172 13H4v-2z"
                ></path>
              </svg>
            </div>
          </button>
        </form>
      </div>
    </>
  );
}
